<template>
  <v-app id="app">
    <!-- <v-toolbar app></v-toolbar> -->
    <v-content>
      <v-container class="page-container">
        <v-layout id="profile" wrap justify-center row pa-2>
        <v-flex justify-center pa-2>
            <v-img class="portrait" width=180 src="/static/image/me.jpg"/>
        </v-flex>
        <v-flex grow xs8 md9>
          <v-card flat>
            <v-card-title class="pa-2 display-1" primary-title>
              Hi, I'm Jianping (Kelvin) Li
            </v-card-title>
            <v-card-text class="pa-2">
              <p class="text-xs-left subheading">
                I am a PhD student in computer science at the University of California, Davis, where I work with <a href="http://web.cs.ucdavis.edu/~/ma">Prof. Kwan-Liu Ma</a> and the Visualization and Interface Design and Innovation (VIDI) Lab. 
              </p>
              <p class="text-xs-left subheading">
                My research interests include data management, visual analytics, and high-performance computing.
                The mission of my research is to help more people understand complex information and discover new knowledge by using data analytics and visualization technologies.
              </p>
            </v-card-text>
          </v-card>
        </v-flex>
        </v-layout>
        <Projects ref="FeaturedProjects" headline="Featured Projects"/>
        <div>
        <v-layout justify-center row>
          <v-flex xs8>
            <Publications ref="SelectedPapers" headline="Selected Publications"/>
          </v-flex>
          <v-flex xs4>
            <Talks ref="Talks" headline="Talks"/>
          </v-flex>
        </v-layout>
        </div>
      </v-container>
    </v-content>
    <v-footer app></v-footer>
  </v-app>
</template>

<script>
import Projects from './components/Projects'
import Publications from './components/Publications'
import Talks from './components/Talks'
import profile from './profile.yaml'

export default {
  name: 'App',
  components: {
    Projects,
    Publications,
    Talks
  },
  mounted () {
    this.$refs.FeaturedProjects.setProjects(profile.projects.filter(p => p.featured))
    this.$refs.SelectedPapers.setPublications(profile.publications.filter(p => p.featured))
    this.$refs.Talks.setTalks(profile.talks)
  }
}
</script>

<style>
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
  font-family: Lato;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: #fff;
  /* color: #2c3e50; */
  border-top: 6px solid steelblue;
}

h2 {
  color: steelblue;
  font-weight: bolder;
}

#profile {
  background-color: #fff;
}

.page-container {
  max-width: 1028px!important;
}

.portrait {
  text-align: center;
  margin: auto;
}

.portrait div{
  border-radius: 50%;
  margin: auto;
}


</style>



