<template>
  <div style="margin-top: 2em;">
    <h2 class="title text-xs-left pa-2 project-card-headline">{{headline}}</h2>
    <v-layout wrap justify-center row style="background-color: #fff;">
      <template v-for="(publication, index) in publications">
        <v-flex :key=index>
          <v-card flat class="ma-0 pa-2" >
            <v-card-text class="ma-1 pa-0 text-xs-left">
              <span class="publication-title">{{publication.title}}</span>,
              <span>{{publication.venue}}, {{publication.year}}</span>
            </v-card-text>
            <v-card-text class="publication-authors ma-1 pa-0" v-html="listAuthors(publication.authors)"></v-card-text>
            <v-card-text v-if="publication.note" class="caption pa-0 ma-0 text-xs-left">{{publication.note}}</v-card-text>
          </v-card>
        </v-flex>
      </template>
    </v-layout>
  </div>
</template>

<script>
export default {
  name: 'Projects',
  props: ['headline'],
  data () {
    return {
      publications: []
    }
  },
  methods: {
    setPublications (publications) {
      this.publications = publications
    },
    listAuthors (authors) {
      return authors.map( author => {
        if (author.slice(0,8) == 'Jianping') {
          return '<span class="author-is-me">' + author + '</span>'
        } else {
          return '<span>' + author + '</span>'
        }
      }).join(', ')
    }
  }
}
</script>

<style>

.publication-title {
  text-align: left;
  font-size: 1em;
  font-weight: bold;
}

.publication-authors {
  text-align: left;
  font-style: italic;
  color: #333;
  font-size: 0.9em;
}

.author-is-me {
  font-weight: bold;
  text-decoration: underline;
}
</style>



