<template>
  <v-list style="margin-top: 2em;">
    <h2 class="title text-xs-left">{{headline}}</h2>
    <template v-for="(talk, index) in talks">
      <v-list-tile
        :key=index
        avatar
        ripple
        style="height: 3em"
      >
        <v-list-tile-action>
          <v-list-tile-action-text style="color: #666">{{ talk.date }}</v-list-tile-action-text>
        </v-list-tile-action>
        <v-list-tile-content class="pa-1" style="font-size: 0.8em">
          {{ talk.for }}, {{ talk.location }}
        </v-list-tile-content>
      </v-list-tile>
    </template>
  </v-list>
</template>

<script>
export default {
  name: 'Projects',
  props: ['headline'],
  data () {
    return {
      talks: []
    }
  },
  methods: {
    setTalks (talks) {
      this.talks = talks
    }
  }
}
</script>

<style>

.talk-title {
  text-align: left;
  font-size: 1em;
  font-weight: bold;
}

.talk-authors {
  text-align: left;
  font-style: italic;
  color: #333;
  font-size: 0.9em;
}

.author-is-me {
  font-weight: bold;
  text-decoration: underline;
}
</style>



